import React from 'react';
import PropTypes from 'prop-types';
import {
  IconBlank,
  IconExternal,
  IconFolder,
  IconGitHub,
  IconHackTheBox,
  IconKey,
  IconLoader,
  IconLogo,
  IconMastodon,
} from '@components/icons';

const Icon = ({ name }) => {
  switch (name) {
    case 'Blank':
      return <IconBlank />;
    case 'External':
      return <IconExternal />;
    case 'Folder':
      return <IconFolder />;
    case 'GitHub':
      return <IconGitHub />;
    case 'HackTheBox':
      return <IconHackTheBox />;
    case 'Key':
      return <IconKey />;
    case 'Loader':
      return <IconLoader />;
    case 'Logo':
      return <IconLogo />;
    case 'Mastodon':
      return <IconMastodon />;
    default:
      return <IconExternal />;
  }
};

Icon.propTypes = {
  name: PropTypes.string.isRequired,
};

export default Icon;
